import React from "react";

// talons
import { useFinancialForecasting } from "../../talons/FinancialForecasting/useFinancialForecasting";

// utils
import mergeClasses from "../../utils/mergeClasses";

// components
import { Table } from "antd";
import OverviewCard from "../Cards/OverviewCard";
import Loading from "../Loading";

// classes
import defaultClasses from "./financialForecasting.module.css";
import FinancialChart from "./FinancialChart/FinancialChart";
import ForecastChart from "./ForecastChart/ForecastChart";

interface Props {
    classes?: object;
}

const FinancialForecasting = ({ classes: propsClasses }: Props) => {
    const classes = mergeClasses(defaultClasses, propsClasses);

    const { expenses, avgProfit, profits, revenues, forecastProfits } =
        useFinancialForecasting();

    if (
        !revenues ||
        !expenses ||
        !profits ||
        !revenues.length ||
        !expenses.length ||
        !profits.length
    ) {
        return <Loading />;
    }

    const totalExpense =
        expenses?.length > 0 &&
        expenses.reduce((res: number, curr: number) => (res += curr), 0);

    const totalProfit =
        profits?.length > 0 &&
        profits.reduce((res: number, curr: number) => (res += curr), 0);

    const totalRevenue =
        revenues?.length > 0 &&
        revenues.reduce((res: number, curr: number) => (res += curr), 0);

    const financialOption = {
        legend: {
            data: ["Doanh thu", "Chi ph", "Li nhun"],
        },
        series: [
            {
                name: "Doanh thu",
                type: "bar",
                data: revenues,
            },
            {
                name: "Chi ph",
                type: "bar",
                data: expenses,
            },
            {
                name: "Li nhun",
                type: "line",
                data: profits,
            },
        ],
    };

    const forecastOption = {
        series: [
            {
                type: "bar",
                showBackground: true,
                data: forecastProfits,
            },
        ],
    };

    return (
        <section className={classes.root}>
            <section className={classes.statistic}>
                <OverviewCard name="Tng Chi ph" number={totalExpense || 0} />
                <OverviewCard
                    name="Tng Doanh thu"
                    number={totalRevenue || 0}
                />
                <OverviewCard name="Tng Li nhun" number={totalProfit || 0} />
            </section>
            <FinancialChart
                title={`Thng k chi ph, doanh thu v li nhun nm ${new Date().getFullYear()}`}
                option={financialOption}
                width={"100%"}
                padding="2rem"
            />
            <p className={classes.summary}>
                Tng trng trung bnh mi thng ca nm{" "}
                {new Date().getFullYear()} l:
                <span
                    style={{
                        color: avgProfit > 0 ? "green" : "red",
                    }}
                >
                    {avgProfit} %
                </span>
            </p>
            <section className={classes.forecast}>
                <ForecastChart
                    title="D on li nhun 2 nm ti:"
                    option={forecastOption}
                    width={1000}
                />
            </section>
        </section>
    );
};

export default FinancialForecasting;
